<template>
  <div class="cards-container">
    <div
      class="uk-child-width-1-1@s uk-child-width-1-2@m uk-child-width-1-3@l uk-child-width-1-4@xl"
      uk-grid="masonry: true"
      uk-lightbox="animation: slide"
    >
      <div v-for="img in images.data" :key="img.id">
        <Card :img-data="img" />
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Card from "@/components/Card.vue";

import axios from "axios";

export default {
  name: "CardsContainer",
  components: {
    Card
  },
  props: {
    dataLink: String
  },
  data: () => ({
    images: []
  }),
  methods: {
    getJSONFromLink(link) {
      axios
        .get(link)
        .then(response => {
          this.images = response.data;
        })
        .catch(err => console.alert(err));
    }
  },
  created() {
    this.getJSONFromLink(this.dataLink);
  }
};
</script>

<style scoped>
.uk-card-default {
  background: #fff;
  color: #666;
  box-shadow: 0 5px 15px rgb(0 0 0 / 8%);
}
</style>
